@import "../styles/variables";

.content {
  margin-top: $space-4;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.icon {
  width: 88px;
  height: 88px;
  margin-bottom: $space-3;
}

.title {
  font-weight: 600;
  font-size: 28px;
  line-height: 40px;
  text-align: center;
}

.text {
  font-weight: 500;
  font-size: 18px;
  text-align: center;
  line-height: $space-3;
  padding: 0 $space-2;
}
