@import "../styles/variables";

$size-answer-button: 20px;

.answerContainer {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  width: 100%;
  height: 100%;
}

.answerInput {
  width: 100%;

  input {
    border-color: $color-warning;
    padding-right: calc(#{$size-answer-button} + #{$space-2} + #{$space-2});
  }
}

.correctAnswerInput input {
  background-color: $color-warning;
  color: #0F0F0F;
}

.answerButton {
  background: none;
  border: none;
  padding: $space-1;
  line-height: 0;
}

.answerButtonIcon {
  width: $size-answer-button;
  height: $size-answer-button;
  fill: $color-warning;
}

.correctButtonIcon {
  fill: #0F0F0F;
}
