@import "../../styles/variables";

.inventoryList {
  margin-top: -($space-2);
  margin-right: -($space-2);
  margin-left: -($space-2);
  padding: $space-2;
  background: rgba(0, 0, 0, 0.5);
}

.noInventoryMessage {
  font-weight: 700;
  margin-bottom: $space-2;
  text-align: center;
}

.inventoryWrapper {
  overflow-x: auto;
}

.inventoryItems {
  display: flex;
  justify-content: space-between;
}

.inventoryPlaceholder {
  min-width: 72px;
  height: 72px;
  background: $main-gradient;
  border: 1px dashed $color-secondary-text;
  border-radius: 6px;
  margin-right: $space-2;

  &:last-child {
    margin-right: 0;
  }
}
.inventoryItem {
  margin-right: $space-2;
}
