@import "../styles/variables";

.install {
  width: 100%;
  min-height: 100vh;
  background: #ffffff;
  color: #000000;
  font-family: 'PT Mono', monospace;
  font-size: 14px;
  padding: 16px;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

.installContent {
  max-width: $content-max-width;
  margin: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.icon {
  align-self: center;
  width: 136px;
  height: 136px;
  background: linear-gradient(191.4deg, #1E2563 0.92%, #0D123A 99.99%, #0E1446 100%);
  box-shadow: 0 0.68px 0 rgba(0, 0, 0, 0.4), 0 2.04px 2.72px rgba(0, 0, 0, 0.3), 0 2.72px 6.8px rgba(0, 0, 0, 0.5);
  border-radius: 12px;
}

.iconImg {
  width: 100%;
  height: 100%;
}

.title {
  font-size: 18px;
  font-weight: 700;
  margin-top: 36px;
  margin-bottom: 16px;
}
