@import "../styles/variables";

$height-mission: 10px;

.accordion {
  margin-right: -($space-2);
  margin-left: -($space-2);
}

.accordionPanel {
  border-bottom: none;
}

.mission {
  background: #4A3B7D;
  position: relative;
  margin-bottom: calc(#{$height-mission} * 2);

  &:before,
  &:after {
    content: '';
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    display: block;
    height: $height-mission;
    border-radius: 2px;
  }

  &:before {
    bottom: -$height-mission;
    width: 96%;
    background: #36266A;
  }

  &:after {
    bottom: calc(-#{$height-mission} - #{$height-mission});
    width: 90%;
    background: #2A1D59;
  }
}

.collapsed {
  border-bottom: 1px solid #372A63;
}

.header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: $space-2;
}

.headerLeft {
  display: flex;
  align-items: center;
  max-width: 97%;
}

.status {
  color: $color-secondary-text;
  font-weight: 700;
  margin-right: $space-1;
}

.icon {
  margin-right: $space-2;
}

.checkedIcon,
.uncheckedIcon,
.collapsedUncheckedIcon {
  width: 20px;
  height: 20px;
  margin-right: $space-1;
  flex-shrink: 0;
}

.checkedIcon,
.uncheckedIcon {
  fill: $color-warning;
}

.collapsedUncheckedIcon {
  fill: $color-secondary-text;
}

.title {
  font-weight: bold;
  font-size: 16px;
  line-height: initial;
  letter-spacing: initial;
  text-overflow: ellipsis;
  white-space:nowrap;
  overflow: hidden;
  width: 100%;
}

.hidden {
  display: none;
}

.arrowIcon {
  width: 8px;
  height: 12px;
  fill: $color-secondary-text;
  transform: rotate(-90deg);
  flex-shrink: 0;
}

.collapsedArrowIcon {
  transform: rotate(0);
}

.content {
  padding: $space-2;
}
