@import "../../styles/variables";

.inventoryItems {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.inventory {
  display: flex;
  margin-bottom: $space-1;
}

.inventoryInfo {
  display: flex;
  flex-direction: column;
  justify-content: center;
  margin-left: 12px;
}

.inventoryTitle {
  font-weight: 700;
  margin-bottom: $space-1;
}

.inventoryName {
  font-size: 16px;
}
