@import "../../styles/variables";

.urlButton,
.videoWrapper,
.imageWrapper {
  margin-bottom: $space-4;
}
.urlButton {
  display: flex;
  justify-content: center;
  width: 100%;
}

.videoWrapper {
  width: 100%;
  padding: 56.25% 0 0 0;
  position: relative;
}

.videoIframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}
