@import "../../styles/variables";

.player {
  display: flex;
  align-items: center;
  margin-bottom: $space-2;
}

.playerInfo {
  margin-left: $space-1;
}

.nickname {
  font-weight: 700;
  font-size: 18px;
  line-height: 16px;
  margin-bottom: calc(#{$space-1} / 2);
}

.email {
  font-weight: 500;
  font-size: 14px;
  line-height: 16px;
  color: $color-secondary-text;
}
