@import "../styles/variables";

$header-height: 72px;
$footer-height: 105px;
$footer-with-gradient-height: 120px;
$footer-with-action-height: 128px;

.screen {
  position: relative;
}

.headerContainer {
  position: fixed;
  top: 0;
  width: 100%;
  display: flex;
  justify-content: center;
  z-index: 1;
}

.header {
  width: 100%;
  max-width: $content-max-width;
  height: $header-height;
  background: #1E2563;
  padding-right: $space-2;
  padding-left: $space-2;
  display: flex;
  align-items: center;
}

.backIcon {
  width: 16px;
  height: 16px;
  fill: #ffffff;
  margin-right: $space-2;
}

.contentContainer {
  min-height: 100vh;
  display: flex;
  justify-content: center;
}

.content {
  width: 100%;
  max-width: $content-max-width;
  background: $main-gradient;
  padding: calc(#{$header-height} + #{$space-2}) $space-2 calc(#{$footer-height} + #{$space-2});
}

.footerContainer {
  position: fixed;
  bottom: 0;
  width: 100%;
  display: flex;
  justify-content: center;
}

.footer {
  width: 100%;
  max-width: $content-max-width;
  height: $footer-height;
  padding-right: $space-2;
  padding-left: $space-2;
}

.footerCentered {
  display: flex;
  justify-content: center;
  align-items: center;
}

.withoutHeader {
  .content {
    padding-top: $space-3;
  }
}

.footerWithGradient {
  .content {
    padding-bottom: calc(#{$footer-with-gradient-height} + #{$space-2});
  }

  .footer {
    height: $footer-with-gradient-height;
    background: $footer-gradient;
  }
}

.footerWithActions {
  .content {
    padding-bottom: calc(#{$footer-with-action-height} + #{$space-2});
  }

  .footer {
    height: $footer-with-action-height;
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}
