@import "../styles/variables";

.button {
  border: none;

  &:active {
    opacity: 0.9;
  }
}

.main,
.secondary {
  width: 295px;
  height: 56px;

  background: radial-gradient(100% 100% at 50.17% 0%, #AD99FF 0%, #3446E9 100%);
  border-radius: 35px;

  font-size: 18px;
  color: #ffffff;
}

.secondary {
  background: transparent;
  border: 1px solid $color-secondary-text;
}

.dark {
  height: 40px;

  padding: 12px $space-2;
  background: rgba(0, 0, 0, 0.5);
  border-radius: 32px;

  font-weight: bold;
  font-size: 14px;
  line-height: 16px;

  color: $color-secondary-text;
}
