@import "../../styles/variables";

.header {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.headerActions {
  display: flex;
  align-items: center;
}

.taskButton {
  background: rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  line-height: 0;
  margin-left: 12px;
}

.taskButtonIcon {
  width: 20px;
  height: 9px;
  fill: $color-secondary-text;
}

.prevTaskButton {
  transform: scaleX(-1);
}

$padding-top: $space-2;
$padding-bottom: $space-4;

.missionContent {
  padding-bottom: calc(120px + #{$padding-top} + #{$padding-bottom} + #{$space-2}) !important; // TODO: remove important after reworked Screen.Footer to avoid withGradients, withActions
}

.missionFooter {
  height: calc(120px + #{$space-4}) !important; // TODO: remove important after reworked Screen.Footer to avoid withGradients, withActions
  padding-top: $space-2;
  padding-bottom: $padding-bottom;
}
