@import "../styles/variables";

.carouselControls {
  width: 100%;
  display: flex;
}

.carouselDots {
  flex-basis: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.carouselDot {
  width: 10px;
  height: 10px;
  background: #9E97BC;
  border-radius: 50%;
  margin-right: 10px;

  &:last-child {
    margin-right: 0;
  }
}

.activeDot {
  width: 18px;
  height: 18px;
  background: $color-warning;
}

.carouselButtons {
  flex-basis: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.carouselButton {
  width: 147px;
}
