@import "../../styles/variables";

$inventory-small-size: 65px;
$inventory-large-size: 72px;
$inventory-border-size: 1px;

.inventory {
  position: relative;
  background: $main-gradient;
  border: $inventory-border-size dashed $color-secondary-text;
  border-radius: 8px;
  display: flex;
  justify-content: center;
  align-items: center;
}

.checkedIcon {
  width: 20px;
  height: 20px;
  fill: $color-warning;
  position: absolute;
}

.inventoryImageUsed {
  opacity: 0.5;
}

.small {
  width: $inventory-small-size;
  height: $inventory-small-size;
}

.large {
  width: $inventory-large-size;
  height: $inventory-large-size;
}

.smallImage {
  width: calc(#{$inventory-small-size} - 2 * #{$inventory-border-size});
  height: calc(#{$inventory-small-size} - 2 * #{$inventory-border-size});
}

.largeImage {
  width: calc(#{$inventory-large-size} - 2 * #{$inventory-border-size});
  height: calc(#{$inventory-large-size} - 2 * #{$inventory-border-size});
}
