@import "../styles/variables";

.message {
  border-radius: 5px;
  padding: 12px $space-2;
  margin-bottom: $space-3;
  color: #0F0F0F;
  font-weight: 500;
  display: flex;
  flex-direction: column;
}

.error {
  background: $color-error;
}

.warning {
  background: $color-warning;
}

.info {
  background: #2b305c;
  color: #ffffff;
}
