@import "../styles/variables";

.textInput {
  background: $color-dark;
  border-radius: 5px;
  width: 100%;
  padding: $space-2;
  border: 2px solid transparent;
  box-shadow: none;
  font-size: 18px;
  line-height: 16px;
  font-family: $pt-mono;
  font-weight: 700;
  color: #ffffff;

  &:focus {
    border-color: $color-warning;
    outline: none;
  }
}

.hasErrors {
  border-color: $color-error;
}

.disabled {
  color: #858795;
}

.password {
  padding-right: calc(#{$space-2} * 3);
}

.inputContainer {
  position: relative;
}

.passwordIcon {
  position: absolute;
  top: 50%;
  right: $space-2;
  transform: translateY(-50%);
  width: 24px;
  height: 24px;
  fill: #ffffff;
}

.addonAfter {
  position: absolute;
  top: 50%;
  right: 0;
  transform: translate(-$space-2, -50%);
}
