@import "../../styles/variables";

.gameInfo {
  margin-right: -($space-2);
  margin-left: -($space-2);
  padding: $space-2;
  height: 100%;
  background: #070a1f;
}

.teamInfo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: $space-2;
}

.teamName {
  color: #ffffff;
  font-size: 16px;
  font-weight: 700;
  max-width: calc(100% - 114px);
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.teamMembers {
  color: $color-warning;
  font-weight: 700;
}

.gtIcon {
  width: 6px;
  height: 8px;
  fill: $color-warning;
  margin-left: $space-1;
}
