@import "../../styles/variables";

.vrTaskContainer {
  position: relative;
  min-height: 100vh;
}

.closeButton {
  position: absolute;
  top: $space-3;
  right: $space-3;
  background: rgba(0, 0, 0, 0.5);
  border: none;
  border-radius: 50%;
  width: 40px;
  height: 40px;
  line-height: 0;
}

.closeButtonIcon {
  width: 14px;
  height: 14px;
  fill: $color-secondary-text;
}
