@import "../styles/variables";

.avatarIcon {
  border: 1px solid $color-secondary-text;
}

.round {
  width: 40px;
  height: 40px;
  border-radius: 50%;
}

.roundBig {
  width: 48px;
  height: 48px;
  border-radius: 50%;
}

.square {
  width: 80px;
  height: 80px;
  border-radius: 5px;
}
